.logo-block {
  display: flex;               
  align-items: center;         
  justify-content: space-between; 
  flex-wrap: nowrap;
  padding: 40px;

  overflow-x: auto;            /* –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–∏–π —Å–∫—Ä–æ–ª */
  -webkit-overflow-scrolling: touch; /* –ø–ª–∞–≤–Ω–∏–π —Å–∫—Ä–æ–ª –Ω–∞ iOS */
  scroll-behavior: smooth;

  /* –ø—Ä–∏—Ö–æ–≤–∞—Ç–∏ —Å–∫—Ä–æ–ª–±–∞—Ä */
  scrollbar-width: none;       /* Firefox */
}

.logo-block::-webkit-scrollbar {
  display: none;               /* Chrome, Safari, Edge */
}

.logo-item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 auto; /* –Ω–µ —Å—Ç–∏—Å–∫–∞—Ç–∏ –ª–æ–≥–æ—Ç–∏–ø–∏ —É —Å–∫—Ä–æ–ª—ñ */
  margin-right: 20px;
}

.logo-block .logo-item:first-child img {
  transform: translateY(-20px);
}

.logo-item img {
  height: 50px;
  width: auto;
  display: block;
  transition: transform 0.2s ease;
}

.logo-item img:hover {
  transform: scale(1.1);
}

/* –ê–¥–∞–ø—Ç–∏–≤–Ω—ñ—Å—Ç—å */
@media (max-width: 768px) {
  .logo-block {
    justify-content: flex-start; 
    padding: 20px 10px;
  }

  .logo-block .logo-item:first-child img {
  transform: translateY(-15px);
}

  .logo-item img {
    height: 40px;
  }
}

@media (max-width: 480px) {
  .logo-block {
    padding: 15px 10px;
  }

  .logo-item img {
    height: 30px;
  }
}