.info-block {  /* –≤—ñ–¥—Å—Ç—É–ø–∏ –≤—ñ–¥ –∫—Ä–∞—ó–≤ */
  padding: 40px;
  text-align: left;
  line-height: 1.6;
  font-size: 18px;
  color: #333;
}

.concert-info-list ul {
  list-style: none; /* –ø—Ä–∏–±–∏—Ä–∞—î–º–æ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ñ –º–∞—Ä–∫–µ—Ä–∏ */
  padding-left: 0;
}

.concert-info-list li {
  position: relative;
  padding-left: 20px; /* –≤—ñ–¥—Å—Ç—É–ø –¥–ª—è –º–∞—Ä–∫–µ—Ä–∞ */
  margin-bottom: 10px; /* –≤—ñ–¥—Å—Ç–∞–Ω—å –º—ñ–∂ –µ–ª–µ–º–µ–Ω—Ç–∞–º–∏ */
  font-size: 18px;
  color: #333;
}

.concert-info-list li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0.6em; /* —â–æ–± –º–∞—Ä–∫–µ—Ä –±—É–≤ –ø–æ —Å–µ—Ä–µ–¥–∏–Ω—ñ —Ç–µ–∫—Å—Ç—É */
  width: 10px;
  height: 10px;
  background-color: #d10000; /* —á–µ—Ä–≤–æ–Ω–∏–π –∫–≤–∞–¥—Ä–∞—Ç */
}


@media (max-width: 420px) {
  .info-block {
    padding: 20px 15px;
  }
}