.navfoot {
  background-color: #fff;
  border-top: 1px solid #eee;
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* –ü–µ—Ä—à–∏–π —Ä—è–¥–æ–∫ */
.navfoot-top {
  text-align: center;
}

.navfoot-top .logo {
  font-size: 28px;
  font-weight: bold;
  color: #1a1a1a;
}

.navfoot-top .plus {
  color: #e60000;
  margin-left: 2px;
}

/* –î—Ä—É–≥–∏–π —Ä—è–¥–æ–∫ */
.navfoot-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
}

/* –ù–∞–≤—ñ–≥–∞—Ü—ñ—è –≤ –ª—ñ–≤–æ–º—É –∫—É—Ç—ñ */
.navfoot-nav a {
  margin-right: 20px;
  text-decoration: none;
  color: #444;
  font-weight: 500;
}

.navfoot-nav a:hover {
  color: #3498db;
  text-decoration: underline;
}

/* –ú—ñ—Å—Ç–æ —ñ –º–æ–≤–∞ ‚Äî —É –ø—Ä–∞–≤–æ–º—É –∫—É—Ç—ñ */
.footer-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.footer-right .city {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #555;
  font-weight: 500;
}

.footer-right .location-icon {
  margin-right: 6px;
  font-size: 18px;
}

.footer-right .lang {
  padding: 6px 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 14px;
  cursor: pointer;
}
@media (max-width: 420px) {
  .navfoot {
    padding: 20px 15px;
    gap: 10px;
  }

  /* –õ–æ–≥–æ—Ç–∏–ø –ø–æ —Ü–µ–Ω—Ç—Ä—É */
  .navfoot-top .logo {
    font-size: 24px;
  }

  /* –ü—Ä–∏—Ö–æ–≤—É—î–º–æ –Ω–∞–≤—ñ–≥–∞—Ü—ñ–π–Ω—ñ –ª—ñ–Ω–∫–∏ */
  .navfoot-nav {
    display: none;
  }

  /* –î—Ä—É–≥–∏–π —Ä—è–¥–æ–∫ ‚Äî –≥–µ–æ–ª–æ–∫–∞—Ü—ñ—è –∑–ª—ñ–≤–∞, —Å–µ–ª–µ–∫—Ç —Å–ø—Ä–∞–≤–∞ */
  .navfoot-bottom {
    justify-content: space-between;
    align-items: center;
  }

  .footer-right {
    display: flex;
    align-items: center;
    width: 100%;
  }

  .footer-right .city {
    font-size: 16px;
    color: #555;
    font-weight: 500;
  }

  .footer-right .location-icon {
    font-size: 16px;
    margin-right: 6px;
  }

  .footer-right .lang {
    margin-left: auto; /* —Å–µ–ª–µ–∫—Ç —Å–ø—Ä–∞–≤–∞ */
  }
}