.cart-page {
  padding: 40px;
  font-family: "Inter", sans-serif;
  background-color: #f9f9f9;
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ MOST POPULAR */
.cart-popular-title {
  font-size: 36px;
  font-weight: bold;
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.cart-dot {
  width: 18px;
  height: 18px;
  background-color: #D31F37;
  display: inline-block;
  margin-right: 12px;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∫–æ–Ω—Ç–∞–∫—Ç—ñ–≤ —ñ –∫–æ—à–∏–∫–∞ */
.cart-content {
  display: flex;
  gap: 40px;
  flex-wrap: wrap;
}

/* –õ—ñ–≤–∞ –∫–æ–ª–æ–Ω–∫–∞ */
.cart-contact-info {
  flex: 1;
  min-width: 250px;
}

.cart-section-title {
  font-size: 24px;
  margin-bottom: 20px;
}

.cart-contact-form {
  display: flex;
  flex-direction: column;
}

.cart-input {
  display: block;
  width: 100%;
  padding: 10px 12px;
  margin-bottom: 16px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
}

/* –ü—Ä–∞–≤–∞ –∫–æ–ª–æ–Ω–∫–∞ */
.cart-items {
  flex: 1;
  min-width: 250px;
  background: #F4F4F4;
  padding: 20px;
}

.cart-empty-text {
  font-size: 18px;
}

/* Header –∫–æ—à–∏–∫–∞ */
.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.cart-clear-btn {
  background: none;
  border: none;
  color: #D31F37;
  font-size: 16px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.2s;
  padding: 0;
  margin: 0;
  line-height: 1;
  vertical-align: middle;
  display: flex;
  align-items: center;
}
.cart-clear-btn:hover {
  color: #e04e52;
}

/* –°–ø–∏—Å–æ–∫ —Ç–æ–≤–∞—Ä—ñ–≤ */
.cart-item-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.cart-item {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 20px;
  padding: 16px 0;
  border-bottom: 1px solid #ccc;
}

/* –§–æ—Ç–æ –∫–æ–Ω—Ü–µ—Ä—Ç—É */
.cart-item-image {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 8px;
}

/* –Ü–Ω—Ñ–æ—Ä–º–∞—Ü—ñ–π–Ω–∞ —á–∞—Å—Ç–∏–Ω–∞ */
.cart-item-details {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.cart-item-concert-name {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 8px;
}

/* –†—è–¥–∫–∏ –¥–∞—Ç–∞/–∫—Ä–∞—ó–Ω–∞/–ª–æ–∫–∞—Ü—ñ—è –∑ —ñ–∫–æ–Ω–∫–∞–º–∏ */
.cart-item-line {
  display: flex;
  align-items: center;
  font-size: 16px;
  margin: 2px 0;
  color: #333;
}

.cart-icon {
  margin-right: 6px;
  color: gray;
  font-size: 18px;
}

/* –¢–∏–ø —Ç–∞ –∫—ñ–ª—å–∫—ñ—Å—Ç—å –∫–≤–∏—Ç–∫—ñ–≤ */
.cart-item-type-count {
  margin-top: 8px;
  font-size: 15px;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —Ü—ñ–Ω–∏ */
.cart-item-price-container {
  display: flex;
  align-items: center;
}

.cart-item-price {
  font-weight: bold;
  font-size: 18px;
  color: #D31F37;
}




.cart-summary {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.cart-total {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  font-weight: bold;
}

.cart-total-price {
  color: #D31F37;
}

.cart-agree {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #333;
}

.cart-agree input {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.cart-pay-btn {
  padding: 12px 20px;
  background-color: #D31F37;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.2s;
}

.cart-pay-btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.cart-pay-btn:hover:enabled {
  background-color: #e04e52;
}

/* –ê–¥–∞–ø—Ç–∏–≤ –¥–ª—è –º–æ–±—ñ–ª—å–Ω–∏—Ö */
/* –ê–¥–∞–ø—Ç–∏–≤ –¥–ª—è –º–æ–±—ñ–ª—å–Ω–∏—Ö */
@media (max-width: 420px) {
  .cart-content {
    flex-direction: column;
    gap: 15px;
  }

  .cart-contact-info,
  .cart-items {
    flex: 1 1 100%;
    min-width: auto;
    padding: 0 10px;
  }

  .cart-contact-form {
    flex-direction: column;
    gap: 8px;
  }

  .cart-input {
    width: 100%;
    box-sizing: border-box;
    padding: 6px 8px;
    font-size: 13px;
  }

  .cart-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    padding: 10px 0;
  }

  .cart-item-image {
    width: 100%;
    max-width: 200px;
    height: auto;
    margin-bottom: 8px;
  }

  .cart-item-details {
    width: 100%;
  }

  .cart-item-line,
  .cart-item-type-count {
    font-size: 13px;
  }

  .cart-item-price {
    font-size: 15px;
  }

  .cart-summary {
    gap: 10px;
    padding: 0 10px;
  }

  .cart-total {
    font-size: 15px;
  }

  .cart-agree {
    font-size: 12px;
  }

  .cart-pay-btn {
    font-size: 13px;
    padding: 8px 14px;
  }

  .cart-popular-title {
    font-size: 24px;
  }

  .cart-section-title {
    font-size: 18px;
  }

  .cart-clear-btn {
    font-size: 14px;
  }
}
