.contacts-block {
  display: flex;
  justify-content: space-between; /* –æ—Å–Ω–æ–≤–Ω–µ –≤–∏—Ä—ñ–≤–Ω—é–≤–∞–Ω–Ω—è */
  align-items: flex-start;
  padding: 40px;
  background-color: #f9f9f9;
  color: #282828;
  flex-wrap: wrap; /* –¥–ª—è –∞–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç—ñ */
  gap: 30px;
}

.contacts-left {
  flex: 1 1 300px;
  max-width: 475px; /* –º—ñ–Ω—ñ–º–∞–ª—å–Ω–∞ —à–∏—Ä–∏–Ω–∞ 300px */
}

.contacts-right {
  flex: 0 0 auto;        /* –±–ª–æ–∫ –Ω–µ —Ä–æ–∑—Ç—è–≥—É—î—Ç—å—Å—è */
  margin-left: auto;      /* —à—Ç–æ–≤—Ö–∞—î –±–ª–æ–∫ –≤–ø—Ä–∞–≤–æ */
  text-align: right;      /* —Ç–µ–∫—Å—Ç –≤—Å–µ—Ä–µ–¥–∏–Ω—ñ –≤–∏—Ä—ñ–≤–Ω—é—î–º–æ –ø–æ –ø—Ä–∞–≤–æ–º—É –∫—Ä–∞—é */
  min-width: 200px;       /* –º—ñ–Ω—ñ–º–∞–ª—å–Ω–∞ —à–∏—Ä–∏–Ω–∞ */
}

.contacts-left h3,
.contacts-right h3 {
  font-size: 20px;
  margin: 20px 0; /* –≤–µ—Ä—Ö–Ω—ñ–π —ñ –Ω–∏–∂–Ω—ñ–π –≤—ñ–¥—Å—Ç—É–ø –ø–æ 20px */
  color: #1a1a1a;
}

.contacts-left p, .contacts-right p {
  margin: 6px 0;
  line-height: 1.5;
  font-size: 16px;
}



@media (max-width: 420px) {
  .contacts-block {
    display: flex;
    flex-direction: column; /* –±–ª–æ–∫–∏ –æ–¥–∏–Ω –ø—ñ–¥ –æ–¥–Ω–∏–º */
    align-items: center;    /* —Ü–µ–Ω—Ç—Ä—É—î–º–æ –±–ª–æ–∫–∏ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—ñ */
    text-align: center;     /* —Ç–µ–∫—Å—Ç –≤—Å–µ—Ä–µ–¥–∏–Ω—ñ –±–ª–æ–∫—ñ–≤ */
    gap: 20px;
    padding: 20px 15px;
  }

  .contacts-left,
  .contacts-right {
    flex: 1 1 100%;
    max-width: 100%;
    margin-left: 0;       /* –ø—Ä–∏–±–∏—Ä–∞—î–º–æ –≤—ñ–¥—Å—Ç—É–ø–∏ */
    text-align: center;   /* —Ü–µ–Ω—Ç—Ä—É—î–º–æ —Ç–µ–∫—Å—Ç */
  }

  .contacts-left h3,
  .contacts-right h3 {
    font-size: 18px;
  }

  .contacts-left p,
  .contacts-right p {
    font-size: 15px;
    margin: 4px 0;
  }
}