.ticket-description {
  padding: 40px;
  background: #f9f9f9;
  position: relative;
  font-family: "Inter", sans-serif;
  padding-bottom: 100px;
}

.ticket-title {
  font-family: "Inter", sans-serif;
  font-size: 48px; /* –ø–æ—Ç—Ä—ñ–±–Ω–∏–π —Ä–æ–∑–º—ñ—Ä */
  font-weight: bold;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
}

.ticket-dot {
  width: 18px;
  height: 18px;
  background: #D31F37;
  display: inline-block;
  margin-right: 8px;
}


/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ –∫–æ–Ω—Ü–µ—Ä—Ç—É –ø—ñ—Å–ª—è –¥–æ—Ç—É */
.concert-header {
  font-size: 18px; /* –º–æ–∂–Ω–∞ –ø—ñ–¥–∫–æ—Ä–∏–≥—É–≤–∞—Ç–∏ –∑–∞ –ø–æ—Ç—Ä–µ–±–∏ */
  font-weight: bold;
  margin-bottom: 24px; /* –≤—ñ–¥—Å—Ç—É–ø –º—ñ–∂ –∑–∞–≥–æ–ª–æ–≤–∫–æ–º —ñ –∞–±–∑–∞—Ü–∞–º–∏ */
  line-height: 1.3;
}

/* –ê–±–∑–∞—Ü–∏ –æ–ø–∏—Å—É –∫–æ–Ω—Ü–µ—Ä—Ç—É */
.ticket-description p {
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 16px; /* –≤—ñ–¥—Å—Ç—É–ø –º—ñ–∂ –∞–±–∑–∞—Ü–∞–º–∏ */
}

/* –û—Å—Ç–∞–Ω–Ω—ñ–π –∞–±–∑–∞—Ü –Ω–µ –º–∞—î –Ω–∏–∂–Ω—å–æ–≥–æ –≤—ñ–¥—Å—Ç—É–ø—É */
.last-p {
  padding-bottom: 40px; /* –≤—ñ–¥—Å—Ç—É–ø –æ—Å—Ç–∞–Ω–Ω—å–æ–≥–æ –∞–±–∑–∞—Ü—É */
}

.ticket-options {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  gap: 16px; /* –≤—ñ–¥—Å—Ç—É–ø –º—ñ–∂ —Ä—è–¥–∫–∞–º–∏ */
}

.ticket-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ticket-name {
  font-size: 18px;
  font-weight: 500;
}

.ticket-controls {
  display: flex;
  align-items: center;
  gap: 12px;
}

.ticket-price {
  font-size: 18px;
  font-weight: bold;
}

.ticket-quantity {
  display: flex;
  align-items: center;
  gap: 8px;
}

.ticket-quantity button {
  width: 28px;
  height: 28px;
  border: 1px solid #ccc;
  background: #fff;
  cursor: pointer;
  border-radius: 4px;
}

.cart-button {
  position: absolute;
  background-color: #D31F37; /* —á–µ—Ä–≤–æ–Ω–∞ –∑–∞–ª–∏–≤–∫–∞ */
  color: white;              /* –±—ñ–ª–∏–π –∫–æ–ª—ñ—Ä —Ç–µ–∫—Å—Ç—É */
  font-size: 18px;           /* —à—Ä–∏—Ñ—Ç 18px */
  border: none;              /* –±–µ–∑ —Ä–∞–º–∫–∏ */
  padding: 12px 24px;        /* –≤–Ω—É—Ç—Ä—ñ—à–Ω—ñ –≤—ñ–¥—Å—Ç—É–ø–∏ */
  cursor: pointer;           /* –∫—É—Ä—Å–æ—Ä —É –≤–∏–≥–ª—è–¥—ñ —Ä—É–∫–∏ */
  border-radius: 6px;        /* –∑–∞–∫—Ä—É–≥–ª–µ–Ω—ñ –∫—É—Ç–∏ */         
  transition: 0.2s all; 
  right: 10%;  
  margin-top: 20px;
  margin-bottom: 40px;  /* –ø–ª–∞–≤–Ω–∞ –∞–Ω—ñ–º–∞—Ü—ñ—è –ø—Ä–∏ —Ö–æ–≤–µ—Ä—ñ */
  cursor: pointer; 
  min-width: 360px;
}

.cart-button:hover {
  background-color: #e04e52; /* —Å–≤—ñ—Ç–ª—ñ—à–∏–π —á–µ—Ä–≤–æ–Ω–∏–π –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
}



/* –ú–æ–±—ñ–ª—å–Ω–∞ –∞–¥–∞–ø—Ç–∞—Ü—ñ—è –¥–ª—è –µ–∫—Ä–∞–Ω—ñ–≤ <= 420px */
@media (max-width: 420px) {
  .ticket-description {
    padding: 20px;
    padding-bottom: 120px; /* —â–æ–± –∫–Ω–æ–ø–∫–∞ –Ω–µ –∑–ª–∏–ø–∞–ª–∞—Å—å —ñ–∑ –Ω–∏–∑–æ–º */
  }

  .ticket-title {
    font-size: 20px;
    margin-bottom: 16px;
  }

  .concert-header {
    font-size: 16px;
    margin-bottom: 16px;
  }

  .ticket-description p {
    font-size: 16px;
    line-height: 1.5;
  }

  .ticket-options {
    gap: 12px;
    width: 100%;          /* —â–æ–± –Ω–µ –≤–∏—Ö–æ–¥–∏–≤ –∑–∞ –º–µ–∂—ñ */
    overflow-x: hidden;   /* –∑–∞–ø–æ–±—ñ–≥–∞—î –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–º—É —Å–∫—Ä–æ–ª—É */
  }

  .ticket-row {
    flex-direction: row;      /* –∑–∞–ª–∏—à–∞—î–º–æ —Ä—è–¥–æ–∫, —è–∫ —î */
    justify-content: space-between;
    flex-wrap: wrap;          /* —è–∫—â–æ –Ω–µ –≤–ª–∞–∑–∏—Ç—å, –ø–µ—Ä–µ–Ω–æ—Å–∏–º–æ –µ–ª–µ–º–µ–Ω—Ç–∏ */
    align-items: center;
    gap: 8px;
    width: 100%;
  }

  .ticket-name {
    font-size: 16px;          /* —Ç—Ä–æ—Ö–∏ –º–µ–Ω—à–∏–π —à—Ä–∏—Ñ—Ç */
    flex: 1 1 30%;            /* –¥–æ–∑–≤–æ–ª—è—î –∑–∞–π–º–∞—Ç–∏ —á–∞—Å—Ç–∏–Ω—É —Ä—è–¥–∫–∞ */
  }

  .ticket-controls {
    flex: 1 1 65%;            /* –∫–æ–Ω—Ç—Ä–æ–ª —ñ —Ü—ñ–Ω–∞ –∑–∞–π–º–∞—é—Ç—å —Ä–µ—à—Ç—É */
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 6px;
  }

  .ticket-price {
    font-size: 16px;
  }

  .ticket-quantity {
    gap: 6px;
  }

  .ticket-quantity button {
    width: 24px;
    height: 24px;
  }

  .cart-button {
    position: relative; /* –Ω–µ absolute, —â–æ–± –π—à–ª–∞ –ø—ñ–¥ –∫–≤–∏—Ç–∫–∞–º–∏ */
    width: 100%;
    min-width: 0;
    margin-top: 20px;
    margin-bottom: 40px;
    font-size: 18px;
    padding: 14px 0;
    right: 0;
  }
}
