/* --- –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä DatePicker --- */
.datepicker-wrapper {
  position: relative;
  width: 100%;
  font-family: "Inter", sans-serif;
  box-sizing: border-box;
  user-select: none;
}

/* --- –ü–æ–ª–µ –¥–ª—è –¥–∞—Ç–∏ --- */
.datepicker-wrapper .custom-input {
  width: 100%;
  height: 40px;
  padding: 0 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  background: #fff;
  box-sizing: border-box;
}

/* --- –õ—ñ–≤–∞ —ñ–∫–æ–Ω–∫–∞ –∫–∞–ª–µ–Ω–¥–∞—Ä—è --- */
.datepicker-wrapper .calendar-icon-left {
  display: flex;
  align-items: center;
  margin-right: 6px;
  pointer-events: none;
}

/* --- –¢–µ–∫—Å—Ç –¥–∞—Ç–∏ --- */
.datepicker-wrapper .date-text {
  flex: 1;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #282828;
}

/* --- –°—Ç—Ä—ñ–ª–∫–∞ –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è –∫–∞–ª–µ–Ω–¥–∞—Ä—è --- */
.datepicker-wrapper .arrow-icon {
  display: flex;
  align-items: center;
  transition: transform 0.2s;
}

.datepicker-wrapper .arrow-icon.open {
  transform: rotate(180deg);
}

/* --- –ö–∞–ª–µ–Ω–¥–∞—Ä --- */
.datepicker-wrapper .custom-calendar {
  position: absolute;
  top: 45px;
  left: 0;
  width: 100%;
  max-width: 100%;
  border: none;
  background: #fff;
  z-index: 10;
  padding: 10px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.15);
  box-sizing: border-box;
}

/* --- Header –∫–∞–ª–µ–Ω–¥–∞—Ä—è --- */
.datepicker-wrapper .datepicker-header {
  display: grid;
  grid-template-columns: 40px 1fr 40px; /* –∫–Ω–æ–ø–∫–∞ - –º—ñ—Å—è—Ü—å/—Ä—ñ–∫ - –∫–Ω–æ–ø–∫–∞ */
  align-items: center;
  justify-items: center;
  height: 36px;
  border-bottom: 1px solid #eee;
  background: #fff;
  box-sizing: border-box;
  margin-bottom: 8px;
}

/* --- –ö–Ω–æ–ø–∫–∏ –Ω–∞–≤—ñ–≥–∞—Ü—ñ—ó --- */
.datepicker-wrapper .datepicker-header .nav-arrow {
  width: 100%;
  height: 24px;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;      /* –ø—Ä–∏–±—Ä–∞–ª–∏ —Ñ–æ–Ω */
  border: none;
  color: #D31F37;        /* —á–µ—Ä–≤–æ–Ω–∏–π –∫–æ–ª—ñ—Ä —Å—Ç—Ä—ñ–ª–æ–∫ */
  cursor: pointer;
  padding: 0;
}

/* --- –ú—ñ—Å—è—Ü—å —ñ —Ä—ñ–∫ –ø–æ —Ü–µ–Ω—Ç—Ä—É --- */
.datepicker-wrapper .datepicker-header .current-month {
  text-align: center;
  font-weight: 600;
  color: #D31F37;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* --- –î–Ω—ñ —Ç–∏–∂–Ω—è --- */
.datepicker-wrapper .weekday-row {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  text-align: center;
  color: #999;
  font-weight: 500;
  margin-bottom: 4px;
}

/* --- –î–Ω—ñ –º—ñ—Å—è—Ü—è --- */
.datepicker-wrapper .dates-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 4px;
  text-align: center;
}

.datepicker-wrapper .date-cell {
  padding: 6px 0;
  cursor: pointer;
}

.datepicker-wrapper .date-cell:hover {
  background-color: #f2f2f2;
}

.datepicker-wrapper .date-cell.selected {
  background-color: #D31F37;
  color: white;
}

/* --- –ú–æ–±—ñ–ª—å–Ω–∞ –∞–¥–∞–ø—Ç–∏–≤–Ω—ñ—Å—Ç—å --- */
@media (max-width: 420px) {
  .datepicker-wrapper .custom-input {
    height: 40px;
  }

  .datepicker-wrapper .datepicker-header .nav-arrow {
    width: 20px;
    height: 20px;
    font-size: 12px;
  }
}
